import React from 'react';
import compose from 'recompose/compose';
import {
    FormTab,
    LongTextInput,
    TabbedForm,
    TextInput,
    translate,
    required,
    minLength
} from 'react-admin';

import { functionalityValidate } from './validate';

import withStyles from '@material-ui/core/styles/withStyles';

const filterStyles = {
  status: { 
    width: 150 
  }
};



export const FunctionalityFormSketch = ({ classes, ...props }) => (
  <TabbedForm validate={functionalityValidate} {...props}>
    <FormTab label="Principal">
      <TextInput
        source="item.channel"
        label="Canal"
        formClassName={classes.channel}
        validate={[
          required('Un canal es requerido'),
          minLength(2, 'Debe Ingresar al menos 3 caracteres')
        ]}
      />
      <TextInput
        label="URI de la funcionalidad"
        source="item.uri_functionality"
        fullWidth={true}
        formClassName={classes.uri_functionality}
        validate={[
          required('Una URI de funcionalidad es requerida'),
          minLength(3, 'Debe Ingresar al menos 3 caracteres'),
          (value)=>{
            console.log(value)
            const methodRegExtp = /(GET|POST|DELETE|PUT|PATH)_\/.*/
            if(!methodRegExtp.test(value)){
              return "Formato incorrecto, debe ser precedido por un metodo HTTP estandar y '_/', por ejemplo 'GET_/una-funcionalidad'"
            }
          }
        ]}
      />
      <TextInput
        source="item.intertype"
        label="Intertipo"
        formClassName={classes.intertype}
        validate={[
          required('Un intertipo es requerido'),
          minLength(3, 'Debe Ingresar al menos 3 caracteres')
        ]}
      />
    </FormTab>
    <FormTab label="Configuracion" path="config">
      <LongTextInput
        source="item.config"
        label="Configuracion"
        format={(value) => {
          console.log(value)
          if(typeof value == 'string'){
            return value
          }
          return JSON.stringify(value, null, 2)
        } }
        parse={(value) => {
          try{
            return JSON.parse(value)
          }
          catch(exception){
            console.log('skipping formatting with ', value)
          }
          return value
        }}
        validate={[
          (value)=>{
            if(typeof value != 'object' || Array.isArray(value)){
              return "Formato incorrecto"
            }
          }
        ]}
        formClassName={classes.address}
      />
    </FormTab>
  </TabbedForm>
);


const enhance = compose(
  withStyles(filterStyles),
  translate
);

export const FunctionalityForm = enhance(FunctionalityFormSketch);