import React from 'react';
import compose from 'recompose/compose';
import {
    Edit,
    translate
} from 'react-admin';
import withStyles from '@material-ui/core/styles/withStyles';
import { FunctionalityForm } from '../form';

const EditTitle = ({ record }) => {
  if(record && record.id){
    return <span>Editar functionalidad #{record.id}</span>
  }
  return <span>Cargando</span>;
};

const FunctionalityEditSketch = ({ classes, ...props }) => (
  <Edit title={<EditTitle />} {...props}>
    <FunctionalityForm/>
  </Edit>
);

const functionalityStyles = {

}

const enhance = compose(
  withStyles(functionalityStyles),
  translate
);

export const FunctionalityEdit = enhance(FunctionalityEditSketch);