import axios from 'axios';
import * as _ from 'lodash';

import {
  GET_LIST,
  GET_ONE,
  DELETE_MANY,
  //GET_MANY,
  //GET_MANY_REFERENCE,
  CREATE,
  UPDATE,
  DELETE,
  //fetchUtils,
} from 'react-admin';


//maps view atttributes to model attributes in order to performn an automatic spring JPA sorting

const fieldsMapping = {
  'item.channel': 'channel',
  'item.intertype': 'intertype',
  'item.uri_functionality': 'uri_functionality',
  'item.id_functionality': 'id_functionality'
}

export const FunctionalityProvider=(apiUrl)=>{ 
  return {
    [GET_LIST]:(params) =>{
      const pageBaseOne = _.get(params, 'pagination.page')
      const size = _.get(params, 'pagination.perPage')
      const queryPath = _.get(params, 'filter.query_path')
      const idChannel = _.get(params, 'filter.id_channel')
      let sortField = '';
      const sortFieldLocal = _.get(params, 'sort.field')
      if(sortFieldLocal){
        sortField = fieldsMapping[sortFieldLocal];
      }
      const sortOrder = _.get(params, 'sort.order')
      return axios.get(`${apiUrl}/functionalities/list`, {
        params: {
          page: pageBaseOne && (pageBaseOne - 1),
          size: size,
          query_path: queryPath,
          id_channel: idChannel,
          sort: `${sortField},${sortOrder}`
        }
      })
      .then((response)=>{
        console.log(response)
        return {
          data: response.data.data.map((item)=>{
            return {
              item,
              id: item.id_functionality
            }
          }),
          total: response.data.meta.paging[0].total
        }
      })
    },
    [GET_ONE]:(params)=>{
      return axios.get(`${apiUrl}/functionalities/id/${params.id}`)
      .then((response)=>{
        console.log(response)
        let item = response.data.data;
        return {
          data: {
            item,
            id: item.id_functionality
          }
        }
      })

    },
    [CREATE]:(params)=>{
      console.log(params)
      return axios.post(`${apiUrl}/functionalities/`, params.data.item)
      .then((response)=>{
        console.log(response)
        let item = response.data.data[0];
        return {
          data: {
            item,
            id: item.id_functionality
          }
        }
      })
    },
    [UPDATE]:(params)=>{
      console.log(params)
      return axios.put(`${apiUrl}/functionalities/${params.id}`, params.data.item)
      .then((response)=>{
        console.log(response)
        let item = response.data.data[0];
        return {
          data: {
            item,
            id: item.id_functionality
          }
        }
      })
    },
    [DELETE]:(params)=>{
      console.log(params)
      return axios.delete(`${apiUrl}/functionalities/${params.id}`)
      .then((response)=>{
        console.log(response)
        let item = response.data.data[0];
        return {
          data: {
            item,
            id: item.id_functionality
          }
        }
      })
    },
    [DELETE_MANY]:(params)=>{

      const ids = params.ids;
      const promises = [];
      for (const id of ids) {
        promises.push(axios.delete(`${apiUrl}/functionalities/${id}`)
          .then((response)=>{
            console.log(response)
            let item = response.data.data[0];
            return {
              item,
              id: item.id_functionality
            }
          })
        )
      }
      return Promise.all(promises).then((items)=>{ 
        return {
          data: items
        }
      })
      
    }
  }
}