import React from 'react';
import compose from 'recompose/compose';
import {
    Create,
    translate
} from 'react-admin';
import withStyles from '@material-ui/core/styles/withStyles';
import { FunctionalityForm } from '../form';
import { functionalityModel } from './model';

const CreateTitle = ({ record }) => {
  return <span>Crear nueva functionalidad </span>
};


const getFunctionalityTemplate = ()=>{
  const result = {
    item:  JSON.parse(JSON.stringify(functionalityModel))
  }
  result.item.id_functionality = (new Date()).getTime()
  console.log(result);
  result.item.channel = 'ONB'
  return result
}

const FunctionalityCreateSketch = ({ classes, ...props }) => {
  return (
    <Create title={<CreateTitle />} {...props}>
      <FunctionalityForm defaultValue={getFunctionalityTemplate} />
    </Create>
  )
};

const functionalityStyles = {

}

const enhance = compose(
  withStyles(functionalityStyles),
  translate
);

export const FunctionalityCreate = enhance(FunctionalityCreateSketch);