
import { FunctionalityProvider } from './functionality';

const apiHost = window['API_HOST'];
const apiUrl = `${apiHost}/v1/pom`

var providers = {
  functionality: FunctionalityProvider(apiUrl)
}

export const RootProvider = (type, resource, params) =>{
  console.log({type, resource, params})
  return providers[resource][type](params);
}