import React from 'react';
import compose from 'recompose/compose';
import {
  Filter,
  translate,
  TextInput,
} from 'react-admin';

import withStyles from '@material-ui/core/styles/withStyles';

const filterStyles = {
  status: { 
    width: 150 
  }
};

export const FunctionalityFiltersSketch = ({ classes, ...props }) => (
    <Filter {...props}>
        <TextInput source="id_channel" label="id channel" alwaysOn/>
        <TextInput source="query_path" label="query path" alwaysOn/>
    </Filter>
);


const enhance = compose(
  withStyles(filterStyles),
  translate
);

export const FunctionalityFilters = enhance(FunctionalityFiltersSketch);