import React from 'react';
import { 
  List, 
  Datagrid, 
  TextField, 
  Responsive, 
  EditButton 
} from 'react-admin';
import { FunctionalityTile } from './tile'
import { FunctionalityFilters } from './filters';

const ListTitle = () => {
  return <span>Listado de funcionalidades</span>;
};

export const FunctionalityList = props => (
  <List  title={<ListTitle />} {...props}
    exporter={false}
    filters={<FunctionalityFilters/>}
    >
    <Responsive
      xsmall={
        <FunctionalityTile
          {...props}
        />
      }
      medium={
        <Datagrid rowClick="view">
          <TextField  
            label="ID" 
            source="item.id_functionality" 
          />
          <TextField 
            label="URI" 
            source="item.uri_functionality" 
          />
          <TextField 
            label="Channel" 
            source="item.channel" 
          />
          <TextField 
            label="Intertype" 
            source="item.intertype" 
          />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);